.message {
  width: 100%;
  padding: 1em;
  border: 1px solid #222;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 2em;
  border-radius: 5px;
}

.success {
  color: #155724;
  background-color: #04edda;
  border-color: #c3e6cb;
}

.error {
  color: #723c24;
  background-color: #f8d7da;
  border-color: #e5c6cb;
}
