.homeContainer {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  padding: 4em;
}

.homeContainer h1 {
  font-size: 2.5em;
  margin-bottom: 0.5em;
}

.homeContainer span {
  color: #ffbb33;
  padding: 0 0.2em;
  background-color: #222;
}

.homeContainer p {
  margin-bottom: 1.5em;
  color: #7b7b7b;
}

.homeContainer img {
  width: 350px;
  margin: 2em;
}
